input[type='text'] {
  border: 0;
  width: 100%; }

select {
  border: 0; }

input:focus, button:focus, select:focus {
  outline: none; }

::-webkit-input-placeholder {
  color: inherit; }

:-ms-input-placeholder {
  color: inherit; }

::-ms-input-placeholder {
  color: inherit; }

::placeholder {
  color: inherit; }

body {
  background-color: #e9e9e9; }

.skyline {
  background: url("london.jpg");
  background-size: cover;
  background-position: center center; }

.search-box {
  top: 45%;
  background-color: rgba(53, 126, 221, 0.7); }
  .search-box.has-results {
    top: 0%;
    background-color: #357edd; }

.example-appear {
  opacity: 0.01; }

.example-appear.example-appear-active {
  opacity: 1;
  -webkit-transition: opacity 1s ease-in;
  -o-transition: opacity 1s ease-in;
  transition: opacity 1s ease-in; }

.loader {
  position: absolute;
  left: 50%;
  top: 80%;
  text-align: center;
  margin: 0px auto;
  width: 100%; }

.load-more .loader {
  position: relative;
  top: 0; }



